export const fetchApi = (url, options = {}) => {
  return new Promise((resolve, reject) => {
    fetch(url, options)
      .then((response) => {
        if (response.status < 400) {
          return resolve(response.json())
        } else {
          throw response
        }
      })
      .catch((response) => response.json().then((error) => reject(error)))
  })
}
